@import url(https://fonts.googleapis.com/css2?family=Questrial&display=swap);
body {
  margin: 0;
}

* {
  box-sizing: border-box;
  font-family: "Questrial";
  color: white;
  text-align: center;
  transition: background-color 1.5s ease-in, color 1.5s ease-in;
}

h1 {
  font-size: 50px;
  letter-spacing: 5px;
}

#wrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.quote-box {
  background: white;
  padding: 20px;
  max-width: 700px;
  margin: auto;
  border-radius: 10px;
  transition: height 2s ease-in;
  box-shadow: 10px 20px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
}

.fa.fa-quote-left {
  margin-right: 10px;
}

.quote-text {
  font-size: 36px;
  font-weight: 600;
  letter-spacing: 1.5px;
  line-height: 50px;
}

.quote-author {
  font-size: 28px;
  text-align: right;
  font-style: italic;
  margin: 20px 0;
}

.buttons-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.buttons-container button {
  font-size: 30px;
  box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.19), 0 1px 1px rgba(0, 0, 0, 0.23);
  margin: 3px;
  border-radius: 5px;
  border: 0;
}

.social-media-buttons-container button {
  padding: 0;
}

.shuffle {
  padding: 9px 11px;
}

.buttons-container button:focus {
  outline: 0;
}

.buttons-container button:hover {
  cursor: pointer;
  opacity: 0.9;
}

a {
  display: block;
  padding: 9px 11px;
}

footer {
  font-size: 20px;
  margin-bottom: 20px;
}

